import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

export function createDoorScene(mountNode, initialColor = "#8B5A2B") {
  // Scene setup
  const scene = new THREE.Scene();
  scene.background = new THREE.Color(0xffffff); // Set background to white
  const camera = new THREE.PerspectiveCamera(
    60,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  camera.position.set(3, 4, 8);

  const renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);
  mountNode.appendChild(renderer.domElement);

  // Lights
  const ambientLight = new THREE.AmbientLight(0xffffff, 0.6);
  scene.add(ambientLight);
  const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
  directionalLight.position.set(5, 10, 7.5);
  scene.add(directionalLight);

  // Door geometry (tall, thin rectangle)
  const doorWidth = 3; // feet
  const doorHeight = 6; // feet
  const doorDepth = 0.08;
  const geometry = new THREE.BoxGeometry(doorWidth, doorHeight, doorDepth);

  // Wood-like material (brown color, some roughness)
  const material = new THREE.MeshStandardMaterial({
    color: initialColor,
    roughness: 0.7,
    metalness: 0.2,
  });

  const door = new THREE.Mesh(geometry, material);
  scene.add(door);

  // Add a simple door handle (small metallic sphere)
  const handleGeometry = new THREE.SphereGeometry(0.05, 16, 16);
  const handleMaterial = new THREE.MeshStandardMaterial({ color: 0xcccccc, metalness: 0.8, roughness: 0.3 });
  const handle = new THREE.Mesh(handleGeometry, handleMaterial);
  handle.position.set(doorWidth / 2 - 0.08, 0, 0.09); // right side, center height, just in front
  scene.add(handle);

  // Controls
  const controls = new OrbitControls(camera, renderer.domElement);

  // Animation loop (no rotation)
  let frameId;
  const animate = () => {
    controls.update();
    renderer.render(scene, camera);
    frameId = requestAnimationFrame(animate);
  };
  animate();

  // Handle resize
  const handleResize = () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
  };
  window.addEventListener("resize", handleResize);

  // Color update method
  function setColor(newColor) {
    material.color.set(newColor);
  }

  // Cleanup function
  function cleanup() {
    cancelAnimationFrame(frameId);
    window.removeEventListener("resize", handleResize);
    controls.dispose();
    geometry.dispose();
    material.dispose();
    handleGeometry.dispose();
    handleMaterial.dispose();
    renderer.dispose();
    if (mountNode) {
      mountNode.removeChild(renderer.domElement);
    }
  }

  return { cleanup, setColor };
}
